'use client';

import { useState, useEffect } from 'react';
import { useParams } from 'next/navigation';
import Link from 'next/link';
import Image from 'next/image';
import { StarIcon, ClockIcon, CheckBadgeIcon, UserIcon, ArrowLeftIcon } from '@heroicons/react/24/solid';
import { EnvelopeIcon, ChatBubbleLeftRightIcon } from '@heroicons/react/24/outline';
import { useAuth } from '@/contexts/AuthContext';

// أنواع البيانات
interface Work {
  id: string;
  title: string;
  image: string;
  category: string;
  description: string;
}

interface Review {
  id: string;
  rating: number;
  comment: string;
  date: string;
  user: {
    id: string;
    name: string;
    image?: string;
  };
}

export default function ProviderProfile() {
  const { id } = useParams();
  const [activeTab, setActiveTab] = useState('services');
  const [provider, setProvider] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const { user } = useAuth();
  
  useEffect(() => {
    // محاكاة جلب بيانات مزود الخدمة من API
    const fetchProvider = async () => {
      setLoading(true);
      
      // بيانات تجريبية
      const mockProvider = {
        id: id,
        name: "أحمد خالد",
        title: "مصمم ومطور ذكاء اصطناعي محترف",
        image: "/images/providers/provider1.jpg",
        coverImage: "/images/providers/cover.jpg",
        rating: 4.8,
        reviewsCount: 47,
        projectsCount: 126,
        clientsCount: 86,
        responseRate: 98,
        responseTime: "ساعتين",
        memberSince: "مايو 2022",
        lastActive: "منذ ساعة",
        description: "متخصص في تطوير تطبيقات الذكاء الاصطناعي وتوليد المحتوى الإبداعي باستخدام أحدث تقنيات الذكاء الاصطناعي. خبرة أكثر من 5 سنوات في مجال برمجة Python وتطوير نماذج التعلم الآلي وتطبيقات الويب.",
        skills: ["Python", "TensorFlow", "PyTorch", "JavaScript", "React", "Next.js", "GPT", "DALL-E", "Midjourney", "Stable Diffusion"],
        languages: [
          { name: "العربية", level: "اللغة الأم" },
          { name: "الإنجليزية", level: "متقدم" }
        ],
        education: [
          { degree: "ماجستير علوم الحاسب", institution: "جامعة الملك سعود", year: "2020" },
          { degree: "بكالوريوس هندسة البرمجيات", institution: "جامعة الملك فهد للبترول والمعادن", year: "2018" }
        ],
        certifications: [
          { name: "شهادة مطور الذكاء الاصطناعي المعتمد من Google", year: "2022" },
          { name: "TensorFlow Developer Certificate", year: "2021" },
          { name: "AWS Machine Learning Specialty", year: "2021" }
        ],
        services: [
          { 
            id: "s1", 
            title: "توليد صور احترافية باستخدام Midjourney و DALL-E", 
            category: "توليد صور",
            description: "تصميم صور عالية الجودة باستخدام أحدث نماذج الذكاء الاصطناعي مع تعديلات مخصصة",
            price: 150,
            image: "/images/services/image-generation.jpg",
            rating: 4.9,
            reviewsCount: 28
          },
          { 
            id: "s2", 
            title: "كتابة محتوى إبداعي باستخدام GPT-4", 
            category: "كتابة محتوى",
            description: "كتابة مقالات ومحتوى تسويقي جذاب باستخدام أحدث تقنيات معالجة اللغة الطبيعية",
            price: 120,
            image: "/images/services/article-generation.jpg",
            rating: 4.7,
            reviewsCount: 19
          },
          { 
            id: "s3", 
            title: "تطوير روبوتات محادثة ذكية لخدمة العملاء", 
            category: "الذكاء الاصطناعي",
            description: "إنشاء روبوتات محادثة مخصصة لمساعدة عملائك وتحسين تجربة المستخدم",
            price: 350,
            image: "/images/services/chatbot.jpg",
            rating: 4.8,
            reviewsCount: 12
          }
        ],
        works: [
          { 
            id: "w1", 
            title: "تصميم هوية بصرية باستخدام الذكاء الاصطناعي", 
            category: "توليد صور",
            image: "/images/gallery/image1.jpg",
            description: "تصميم شعار وهوية بصرية كاملة لشركة تقنية ناشئة باستخدام Midjourney وPhotoshop"
          },
          { 
            id: "w2", 
            title: "كتابة محتوى لموقع شركة تقنية", 
            category: "كتابة محتوى",
            image: "/images/gallery/article1.jpg",
            description: "كتابة أكثر من 20 مقالة تقنية متخصصة لموقع شركة برمجيات باستخدام GPT-4 وClaude"
          },
          { 
            id: "w3", 
            title: "تطوير روبوت محادثة لخدمة عملاء", 
            category: "الذكاء الاصطناعي",
            image: "/images/gallery/app1.jpg",
            description: "تطوير وتدريب روبوت محادثة ذكي لشركة تجارة إلكترونية لتحسين خدمة العملاء"
          },
          { 
            id: "w4", 
            title: "تحليل بيانات المبيعات وتوقعات المستقبل", 
            category: "تحليل بيانات",
            image: "/images/gallery/model1.jpg",
            description: "تحليل بيانات المبيعات لشركة تجزئة كبرى وإنشاء نموذج تنبؤي للمبيعات المستقبلية"
          }
        ],
        reviews: [
          {
            id: "r1",
            rating: 5,
            comment: "عمل ممتاز وسريع، تواصل رائع وفهم دقيق للمتطلبات",
            date: "2023-11-15",
            user: {
              id: "u1",
              name: "محمد العبدالله",
              image: "/images/avatars/client1.jpg"
            }
          },
          {
            id: "r2",
            rating: 5,
            comment: "أحمد محترف جدًا، استطاع تنفيذ المطلوب بجودة عالية وقبل الموعد المحدد",
            date: "2023-10-22",
            user: {
              id: "u2",
              name: "سارة الأحمد",
              image: "/images/avatars/client2.jpg"
            }
          },
          {
            id: "r3",
            rating: 4,
            comment: "خدمة جيدة جدًا، لكن كان هناك بعض التأخير في تسليم التعديلات النهائية",
            date: "2023-09-18",
            user: {
              id: "u3",
              name: "فيصل المطيري",
              image: "/images/avatars/client3.jpg"
            }
          }
        ]
      };
      
      setTimeout(() => {
        setProvider(mockProvider);
        setLoading(false);
      }, 500);
    };
    
    fetchProvider();
  }, [id]);
  
  // عرض حالة التحميل
  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="w-12 h-12 rounded-full border-4 border-indigo-300 border-t-indigo-600 animate-spin"></div>
      </div>
    );
  }
  
  // عرض رسالة خطأ إذا لم يتم العثور على مزود الخدمة
  if (!provider) {
    return (
      <div className="min-h-screen flex flex-col items-center justify-center">
        <h2 className="text-2xl font-bold text-gray-900 mb-2">لم يتم العثور على مزود الخدمة</h2>
        <p className="text-gray-600 mb-4">تم حذف الحساب أو أن المعرّف غير صحيح</p>
        <Link href="/providers" className="text-indigo-600 hover:text-indigo-700 font-medium">
          العودة إلى قائمة مزودي الخدمات
        </Link>
      </div>
    );
  }
  
  return (
    <div className="bg-gray-50 min-h-screen">
      {/* الرأس */}
      <div className="relative bg-indigo-600 h-64 md:h-80">
        {provider.coverImage ? (
          <Image
            src={provider.coverImage}
            alt="صورة الغلاف"
            fill
            className="object-cover"
            onError={(e) => {
              const target = e.target as HTMLImageElement;
              target.style.display = 'none';
            }}
          />
        ) : (
          <div className="absolute inset-0 bg-gradient-to-r from-indigo-600 to-blue-500"></div>
        )}
        
        <div className="absolute inset-0 bg-black bg-opacity-20"></div>
        
        <div className="absolute bottom-0 left-0 w-full transform translate-y-1/2 px-4 sm:px-6 lg:px-8">
          <div className="max-w-7xl mx-auto flex items-end">
            <div className="relative h-32 w-32 rounded-lg border-4 border-white bg-white shadow-md overflow-hidden">
              <Image
                src={provider.image || "/images/avatar.jpg"}
                alt={provider.name}
                fill
                className="object-cover"
                onError={(e) => {
                  const target = e.target as HTMLImageElement;
                  target.src = "/images/avatar.jpg";
                }}
              />
              {provider.isVerified && (
                <div className="absolute bottom-0 right-0 bg-green-500 p-1 rounded-full">
                  <CheckBadgeIcon className="h-4 w-4 text-white" />
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
      
      {/* معلومات المستخدم */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 pt-20 pb-6">
        <div className="md:flex md:items-center md:justify-between">
          <div className="md:flex-1">
            <div className="flex items-center">
              <h1 className="text-2xl font-bold text-gray-900 ml-2">{provider.name}</h1>
              {provider.isVerified && (
                <CheckBadgeIcon className="h-5 w-5 text-blue-500" />
              )}
            </div>
            <p className="text-gray-600 mt-1">{provider.title}</p>
            
            <div className="mt-2 flex items-center text-gray-500 text-sm">
              <ClockIcon className="h-4 w-4 ml-1" />
              <span className="ml-3">آخر نشاط: {provider.lastActive}</span>
              <span className="mx-2">•</span>
              <UserIcon className="h-4 w-4 ml-1" />
              <span>عضو منذ: {provider.memberSince}</span>
            </div>
          </div>
          
          <div className="mt-4 md:mt-0 flex flex-wrap gap-2">
            <Link 
              href={`/messages/new?recipient=${provider.id}`}
              className="inline-flex items-center justify-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
            >
              <EnvelopeIcon className="h-4 w-4 ml-2" />
              مراسلة
            </Link>
            
            <Link 
              href={`/service-request/custom?provider=${provider.id}`}
              className="inline-flex items-center justify-center px-4 py-2 border border-gray-300 text-sm font-medium rounded-md shadow-sm text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
            >
              <ChatBubbleLeftRightIcon className="h-4 w-4 ml-2" />
              طلب خدمة مخصصة
            </Link>
          </div>
        </div>
        
        {/* الإحصائيات */}
        <div className="mt-6 grid grid-cols-2 gap-4 md:grid-cols-4">
          <div className="bg-white rounded-lg shadow-sm p-4">
            <p className="text-gray-500 text-sm">التقييم</p>
            <div className="mt-1 flex items-center">
              <p className="text-xl font-semibold text-gray-900">{provider.rating}</p>
              <div className="flex ml-2">
                <StarIcon className="h-5 w-5 text-yellow-400" />
              </div>
              <p className="text-sm text-gray-500 mr-1">({provider.reviewsCount})</p>
            </div>
          </div>
          
          <div className="bg-white rounded-lg shadow-sm p-4">
            <p className="text-gray-500 text-sm">المشاريع المنجزة</p>
            <p className="mt-1 text-xl font-semibold text-gray-900">{provider.projectsCount}</p>
          </div>
          
          <div className="bg-white rounded-lg shadow-sm p-4">
            <p className="text-gray-500 text-sm">نسبة الاستجابة</p>
            <p className="mt-1 text-xl font-semibold text-gray-900">{provider.responseRate}%</p>
          </div>
          
          <div className="bg-white rounded-lg shadow-sm p-4">
            <p className="text-gray-500 text-sm">وقت الاستجابة</p>
            <p className="mt-1 text-xl font-semibold text-gray-900">{provider.responseTime}</p>
          </div>
        </div>
      </div>
      
      {/* نبذة عن المستخدم */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        <div className="bg-white rounded-lg shadow-sm p-6">
          <h2 className="text-xl font-semibold text-gray-900 mb-4">نبذة عني</h2>
          <p className="text-gray-700 whitespace-pre-line">{provider.description}</p>
          
          <div className="mt-6 grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">المهارات</h3>
              <div className="flex flex-wrap gap-2">
                {provider.skills.map((skill: string, index: number) => (
                  <span key={index} className="inline-flex items-center px-3 py-1 rounded-full text-xs font-medium bg-indigo-100 text-indigo-800">
                    {skill}
                  </span>
                ))}
              </div>
            </div>
            
            <div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">اللغات</h3>
              <ul className="space-y-1">
                {provider.languages.map((language: any, index: number) => (
                  <li key={index} className="text-gray-700">
                    <span className="font-medium">{language.name}:</span> {language.level}
                  </li>
                ))}
              </ul>
            </div>
          </div>
          
          <div className="mt-6 grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">التعليم</h3>
              <ul className="space-y-2">
                {provider.education.map((edu: any, index: number) => (
                  <li key={index} className="text-gray-700">
                    <div className="font-medium">{edu.degree}</div>
                    <div className="text-gray-600">{edu.institution} • {edu.year}</div>
                  </li>
                ))}
              </ul>
            </div>
            
            <div>
              <h3 className="text-lg font-medium text-gray-900 mb-2">الشهادات</h3>
              <ul className="space-y-2">
                {provider.certifications.map((cert: any, index: number) => (
                  <li key={index} className="text-gray-700">
                    <div className="font-medium">{cert.name}</div>
                    <div className="text-gray-600">{cert.year}</div>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </div>
      </div>
      
      {/* علامات التبويب */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
        <div className="bg-white rounded-lg shadow-sm">
          <div className="border-b border-gray-200">
            <nav className="-mb-px flex space-x-8 space-x-reverse px-4" aria-label="Tabs">
              <button
                onClick={() => setActiveTab('services')}
                className={`whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm ${activeTab === 'services' ? 'border-indigo-600 text-indigo-600' : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'}`}
              >
                الخدمات ({provider.services.length})
              </button>
              <button
                onClick={() => setActiveTab('works')}
                className={`whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm ${activeTab === 'works' ? 'border-indigo-600 text-indigo-600' : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'}`}
              >
                الأعمال السابقة ({provider.works.length})
              </button>
              <button
                onClick={() => setActiveTab('reviews')}
                className={`whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm ${activeTab === 'reviews' ? 'border-indigo-600 text-indigo-600' : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'}`}
              >
                التقييمات ({provider.reviews.length})
              </button>
            </nav>
          </div>
          
          <div className="p-4">
            {/* الخدمات */}
            {activeTab === 'services' && (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {provider.services.map((service: any) => (
                  <div key={service.id} className="bg-white border border-gray-200 rounded-lg overflow-hidden shadow-sm hover:shadow-md transition-shadow">
                    <div className="relative h-48">
                      <Image
                        src={service.image}
                        alt={service.title}
                        fill
                        className="object-cover"
                        onError={(e) => {
                          const target = e.target as HTMLImageElement;
                          target.style.backgroundImage = 'linear-gradient(to right, #4f46e5, #3b82f6)';
                          target.style.display = 'flex';
                          target.style.alignItems = 'center';
                          target.style.justifyContent = 'center';
                          // غير قادر على وضع نص بشكل مباشر لذا نستخدم لون خلفية فقط
                        }}
                      />
                      <div className="absolute top-2 right-2">
                        <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                          {service.category}
                        </span>
                      </div>
                    </div>
                    
                    <div className="p-4">
                      <Link href={`/services/${service.id}`}>
                        <h3 className="text-lg font-medium text-gray-900 line-clamp-1 hover:text-indigo-600 transition-colors">
                          {service.title}
                        </h3>
                      </Link>
                      
                      <p className="mt-1 text-sm text-gray-500 line-clamp-2">
                        {service.description}
                      </p>
                      
                      <div className="mt-2 flex items-center">
                        <div className="flex items-center">
                          {Array.from({ length: 5 }).map((_, i) => (
                            <StarIcon
                              key={i}
                              className={`h-4 w-4 ${i < Math.floor(service.rating) ? 'text-yellow-400' : 'text-gray-300'}`}
                            />
                          ))}
                        </div>
                        <span className="mr-1 text-sm text-gray-500">
                          {service.rating} ({service.reviewsCount})
                        </span>
                      </div>
                      
                      <div className="mt-4 flex items-center justify-between">
                        <div className="text-lg font-medium text-gray-900">
                          {service.price} ر.س
                        </div>
                        <Link
                          href={`/service-request?service=${service.id}`}
                          className="inline-flex items-center justify-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                        >
                          طلب الخدمة
                        </Link>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
            
            {/* الأعمال السابقة */}
            {activeTab === 'works' && (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {provider.works.map((work: Work) => (
                  <div key={work.id} className="bg-white border border-gray-200 rounded-lg overflow-hidden shadow-sm hover:shadow-md transition-shadow">
                    <div className="relative h-48">
                      <Image
                        src={work.image}
                        alt={work.title}
                        fill
                        className="object-cover"
                        onError={(e) => {
                          const target = e.target as HTMLImageElement;
                          target.style.backgroundImage = 'linear-gradient(to right, #4f46e5, #3b82f6)';
                          target.style.display = 'flex';
                          target.style.alignItems = 'center';
                          target.style.justifyContent = 'center';
                        }}
                      />
                      <div className="absolute top-2 right-2">
                        <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                          {work.category}
                        </span>
                      </div>
                    </div>
                    
                    <div className="p-4">
                      <h3 className="text-lg font-medium text-gray-900 line-clamp-1">
                        {work.title}
                      </h3>
                      
                      <p className="mt-1 text-sm text-gray-500 line-clamp-2">
                        {work.description}
                      </p>
                      
                      <Link
                        href={`/gallery/${work.id}`}
                        className="mt-3 inline-flex items-center text-sm font-medium text-indigo-600 hover:text-indigo-500"
                      >
                        مشاهدة التفاصيل
                        <ArrowLeftIcon className="mr-1 h-4 w-4" />
                      </Link>
                    </div>
                  </div>
                ))}
              </div>
            )}
            
            {/* التقييمات */}
            {activeTab === 'reviews' && (
              <div className="space-y-6">
                <div className="bg-gray-50 rounded-lg p-4 mb-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <div className="flex items-center">
                        <h3 className="text-xl font-semibold text-gray-900">{provider.rating}</h3>
                        <div className="flex mr-1">
                          {Array.from({ length: 5 }).map((_, i) => (
                            <StarIcon
                              key={i}
                              className={`h-5 w-5 ${i < Math.floor(provider.rating) ? 'text-yellow-400' : 'text-gray-300'}`}
                            />
                          ))}
                        </div>
                      </div>
                      <p className="text-gray-600 text-sm">بناءً على {provider.reviewsCount} تقييم</p>
                    </div>
                    
                    {user && user.id !== provider.id && (
                      <Link
                        href={`/reviews/add?provider=${provider.id}`}
                        className="inline-flex items-center justify-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
                      >
                        إضافة تقييم
                      </Link>
                    )}
                  </div>
                </div>
                
                {provider.reviews.map((review: Review) => (
                  <div key={review.id} className="bg-white border border-gray-100 rounded-lg p-4 shadow-sm">
                    <div className="flex">
                      <div className="mr-4 flex-shrink-0">
                        <div className="relative h-10 w-10 rounded-full overflow-hidden">
                          <Image
                            src={review.user.image || "/images/avatar.jpg"}
                            alt={review.user.name}
                            fill
                            className="object-cover"
                            onError={(e) => {
                              const target = e.target as HTMLImageElement;
                              target.src = "/images/avatar.jpg";
                            }}
                          />
                        </div>
                      </div>
                      <div className="flex-1">
                        <div className="flex items-center justify-between">
                          <h4 className="text-sm font-medium text-gray-900">{review.user.name}</h4>
                          <span className="text-sm text-gray-500">
                            {new Date(review.date).toLocaleDateString('ar-SA', { year: 'numeric', month: 'long', day: 'numeric' })}
                          </span>
                        </div>
                        
                        <div className="mt-1 flex items-center">
                          {Array.from({ length: 5 }).map((_, i) => (
                            <StarIcon
                              key={i}
                              className={`h-4 w-4 ${i < review.rating ? 'text-yellow-400' : 'text-gray-300'}`}
                            />
                          ))}
                        </div>
                        
                        <p className="mt-2 text-sm text-gray-700">
                          {review.comment}
                        </p>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
} 